<template>
	<div class="container">
		<div class="title">
			<div class="title-item">
				<div class="title-icon">
					<img style="width: 100%; height: 100%; border-radius: 50%;" src="../assets/2.jpg" />
				</div>
				<span>电商管理系统</span>
			</div>
			<el-row>
				<!-- 内嵌法修改按钮默认样式 -->
				<el-button :style="selfstyle" type="info" @click="loginOut">退出</el-button>
			</el-row>
		</div>
		<el-container>
			<el-container>
				<!-- 侧边栏 -->
				<el-aside :width="isCollapse ? '64px':'200px'">
					<!-- 侧边栏菜单区域 -->
					<div class="toggle-button" @click="toggleCollapse">|||</div>
					<!-- 折叠菜单，绑定布尔值需要动态绑定 -->
					<el-menu background-color="#2d313c" text-color="#fff" active-text-color="#409eff" unique-opened :collapse="isCollapse" router
					:collapse-transition="false" :default-active="activePath"
					>
						<!-- 一级菜单 -->
						<!-- index值都一样会导致点开一个菜单所有菜单一起开，所以动态绑定Index -->
						<!-- 如果只写item.id或报错，下方index只接收字符串，不接受数值，所以加上字符串拼接 -->
						<el-submenu :index="item.id +''" v-for="item in menuList" :key = "item.id">
							<!-- 一级菜单模板区 -->
							<template slot="title">
								<!-- 图标 -->
								<!-- 根据已有的动态id来动态绑定字体图标 -->
								<i :class="iconsObj[item.id]"></i>
								<!-- 文本 -->
								<span>{{item.authName}}</span>
							</template>
							<!-- 二级菜单 -->
							<el-menu-item :index="'/'+ items.path" v-for="items in item.children" :key = "items.id" @click="getStatus('/'+ items.path)">
								<i class="el-icon-menu"></i>
								<span>{{items.authName}}</span>
							</el-menu-item>
						</el-submenu>
					</el-menu>
				</el-aside>
				<el-main>
					<router-view></router-view>
				</el-main>
			</el-container>
		</el-container>
	</div>
</template>

<script>
	import '../assets/font/iconfont.css'
	export default {
		data() {
			return {
				isCollapse:false,
				selfstyle: {
					color: "white",
					marginRight: "20px",
					width: "100px",
					height: "50px",
					backgroundColor: "#909399"
				},
				menuList:[],
				//动态绑定字体图标
				iconsObj:{
					'125':'iconfont icon-user',
					'103':'iconfont icon-tijikongjian',
					'101':'iconfont icon-shangpingouwudai2',
					'102':'iconfont icon-danju-tianchong',
					'145':'iconfont icon-baobiao'
				},
				//激活菜单路径
				activePath:'',

			}
		},
		created(){
			this.getMenuList()
			this.activePath = window.sessionStorage.getItem('activePath')
		},
		methods: {
			//退出登录
			loginOut() {
				//由于前置导航守卫，清空token则只能访问登录页
				window.sessionStorage.clear()
				//跳转到登录页
				this.$router.push("/")
			},
			async getMenuList(){
				const {data:res} = await this.$http.get('menus')
				// console.log(res)
				//如果请求的res.meta.status的状态码不为200则请求失败，通过message组件将错误信息打印出来
				if(res.meta.status != 200) return this.$message.err(res.meta.msg)
				this.menuList = res.data
				// this.menuList.forEach(v=>{
				// 	console.log(v.id)
				// }) 
				// console.log(this.menuList)
			},
			//折叠菜单，通过改变element-ui上折叠菜单的属性collapse
			toggleCollapse(){
				this.isCollapse = !this.isCollapse
			},
			getStatus(activePath){
				//将activeParh放入缓存中，便于刷新后列表选中状态保存
				window.sessionStorage.setItem('activePath',activePath)
				this.activePath = activePath
				console.log(activePath) 
			}
		}
	}
</script>
<style>
	html,
	body {
		overflow: hidden;
		height: 100%;
	}
</style>
<style >
	.iconfont{
		margin-right: 10px;
	}
	.el-submenu .el-menu-item {
		color: white;
	}

	.el-menu-item-group {
		background-color: #2d313c;
	}

	.el-submenu {
		background-color: #2d313c;
	}

	.container {
		width: 100%;
		height: 100%;
		background-color: #2D313C;
	}

	.title {
		display: flex;
		width: 100%;
		height: 90px;
		color: #FFFFFF;
		font-size: 25px;
		align-items: center;
		justify-content: space-between;
	}

	.title-item {
		display: flex;
		align-items: center;
	}

	.title-icon {
		width: 50px;
		height: 50px;
		background-color: white;
		border-radius: 50%;
		margin: 0 15px 0 10px;
	}

	.selfbutton {
		width: 100px;
		height: 50px;
		font-size: 50px;
		font-weight: 600;
		margin-right: 30px;
	}

	.el-container {
		height: 100%;
	}

	.el-aside {
		height: 100%;
		
	}
	/* 去掉边框线 */
	.el-aside .el-menu{
		border-right: none;
	}
	.el-main {
		background-color: white;
		height: 100%;
	}

	.template {
		color: white;
	}
	.toggle-button{
		background-color: #5a6278;
		text-align: center;
		font-size: 10px;
		color: white;
		line-height: 24px;
		letter-spacing: 0.2em;
		cursor: pointer;
	}
</style>
