<template>
	<div>
		<!-- 面包屑区域开始 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>商品管理</el-breadcrumb-item>
			<el-breadcrumb-item>参数列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!--  面包屑区域结束 -->
		<!-- 卡片区域开始 -->
		<el-card class="box-card">
			<!-- 警告提示 -->
			<el-alert show-icon title="注意:  只允许为第三级分类设置相关参数" type="warning">
			</el-alert>
			<div class="chooseCategory">
				<span>选择商品分类：
					<!-- 级联选择器 -->
					<!-- options 指定数据源 -->
					<!-- props 指定配置对象 -->
					<el-cascader expand-trigger="hover" :options="cateList" :props="cascaderProps" v-model="selectedKeys" @change="parentCateChanged"
					 clearable></el-cascader>
				</span>
				<!-- tabs标签页 -->
				<el-tabs v-model="activeName" @tab-click="handleTabClick">
					<!-- 添加动态参数的面板 -->
					<el-tab-pane label="动态参数" name="many">
						<el-button type="primary" :disabled="isBtnDisabled" @click="dialogVisible = true">添加参数</el-button>
						<!-- 表格开始 -->
						<el-table border :data="manyTableData">
							<el-table-column type="expand">
								<template slot-scope="scope">
									<el-tag closable v-for="(item,index) in scope.row.attr_vals" :key="index" @close="handleClose(scope.row,index)">{{item}}</el-tag>
									<el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput"
									 size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
									</el-input>
									<el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

								</template>
							</el-table-column>
							<el-table-column type="index" label="#"></el-table-column>
							<el-table-column prop="attr_name" label="参数名称"></el-table-column>
							<el-table-column prop="email" label="操作">
								<template slot-scope="scope">
									<el-button type="primary" icon="el-icon-edit" size="mini" @click="showDialog(scope.row.attr_id)">修改</el-button>
									<el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
								</template>
							</el-table-column>
						</el-table>
						<!-- 表格结束 -->
					</el-tab-pane>
					<!-- 添加静态属性的面板 -->
					<el-tab-pane label="静态属性" name="only">
						<el-button type="primary" :disabled="isBtnDisabled" @click="dialogVisible = true">添加属性</el-button>
						<!-- 表格开始 -->
						<el-table border :data="onlyTableData">
							<el-table-column type="expand">
								<template slot-scope="scope">
									<el-tag closable v-for="(item,index) in scope.row.attr_vals" :key="index" @close="handleClose(scope.row,index)">{{item}}</el-tag>
									<el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput"
									 size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
									</el-input>
									<el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
								</template>
							</el-table-column>
							<el-table-column type="index" label="#"></el-table-column>
							<el-table-column prop="attr_name" label="属性名称"></el-table-column>
							<el-table-column prop="email" label="操作">
								<template slot-scope="scope">
									<el-button type="primary" icon="el-icon-edit" size="mini" @click="showDialog(scope.row.attr_id)">修改</el-button>
									<el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
								</template>
							</el-table-column>
						</el-table>
						<!-- 表格结束 -->
					</el-tab-pane>
				</el-tabs>
			</div>
			<!-- 添加参数对话框 -->
			<el-dialog :title="'添加' + titleText" :visible.sync="dialogVisible" width="50%" @close="addDiolagClosed">
				<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
					<el-form-item :label="titleText" prop="attr_name">
						<el-input v-model="addForm.attr_name"></el-input>
					</el-form-item>
				</el-form>
				<span slot="footer" class="dialog-footer">
					<el-button @click="dialogVisible = false">取 消</el-button>
					<el-button type="primary" @click="addParams">确 定</el-button>
				</span>
			</el-dialog>
			<!-- 修改参数对话框 -->
			<el-dialog :title="'修改' + titleText" :visible.sync="editDialogVisible" width="50%" @close="editiolagClosed">
				<el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
					<el-form-item :label="titleText" prop="attr_name">
						<el-input v-model="editForm.attr_name"></el-input>
					</el-form-item>
				</el-form>
				<span slot="footer" class="dialog-footer">
					<el-button @click="editDialogVisible = false">取 消</el-button>
					<el-button type="primary" @click="editParams">确 定</el-button>
				</span>
			</el-dialog>
		</el-card>
		<!-- 卡片区域结束 -->
	</div>
</template>

<script>
	export default {
		data() {
			return {
				// 商品分类的数据列表，默认为空
				cateList: [],
				// // 查询条件
				// queryInfo: {
				// 	type: 3,
				// 	pagenum: 1,
				// 	pagesize: 5
				// },
				cascaderProps: {
					value: 'cat_id',
					label: 'cat_name',
					children: 'children'
				},
				// 级联选择框双向绑定的数组
				selectedKeys: [],
				activeName: 'many',
				manyTableData: [],
				onlyTableData: [],
				dialogVisible: false,
				editDialogVisible: false,
				addForm: {
					attr_name: '',
					attr_sel: '',
					attr_vals: ''
				},
				editForm: {
					attr_name: '',
					attr_sel: '',
					attr_vals: ''
				},
				// 添加表单的验证规则
				editFormRules: {
					attr_name: [{
							required: true,
							message: '请输入参数名称',
							trigger: 'blur'
						},
						{
							min: 2,
							max: 10,
							message: '长度在 2 到 10 个字符',
							trigger: 'blur'
						}
					],
				},
				// 添加表单的验证规则
				addFormRules: {
					attr_name: [{
							required: true,
							message: '请输入参数名称',
							trigger: 'blur'
						},
						{
							min: 2,
							max: 10,
							message: '长度在 2 到 10 个字符',
							trigger: 'blur'
						}
					],
				},
				inputVisible: false,
				inputValue: ''
			}
		},
		created() {
			this.getCateList()
		},
		computed: {
			isBtnDisabled() {
				if (this.selectedKeys.length !== 3) {
					return true
				}
				return false
			},
			// 选中三级目录的id
			catId() {
				if (this.selectedKeys.length === 3) {
					return this.selectedKeys[2]
				}
				return null
			},
			titleText() {
				if (this.activeName === "many") {
					return "动态参数"
				}
				return "静态属性"
			},
		},
		methods: {
			// 获取分类列表数据
			async getCateList() {
				const {
					data: res
				} = await this.$http.get("categories")
				if (res.meta.status !== 200) {
					return this.$message.error("获取商品分类数据失败")
				}
				// 将请求到的商品数据赋值给cateList
				this.cateList = res.data
				// console.log(this.cateList)
			},
			// 当级联选择器中的选择框发生改变时，会触发该函数
			parentCateChanged() {
				// 当点击清空级联选择器时selectedKeys数组会清空
				// console.log(this.selectedKeys)
				// 当不是三级目录是就清空列表内的内容，因为二级目录可能不被清空，所以selectedKeys数组需要清空
				if(this.selectedKeys.length !== 3){
					this.selectedKeys = []
					this.manyTableData = []
					this.onlyTableData = []
					return
				}
				this.getParamsData()
			},
			async getParamsData() {
				// console.log(this.selectedKeys)
				// 如果selectedKeys数组的长度不为3，则表明选择的不是三级菜单
				if (this.selectedKeys.length !== 3) {
					this.selectedKeys = []
					return
				}
				// 获取分类参数 
				const {
					data: res
				} = await this.$http.get(`categories/${this.catId}/attributes`, {
					params: {
						sel: this.activeName
					}
				})
				if (res.meta.status !== 200) {
					return this.$message.error("获取分类参数失败")
				}
				// console.log(res.data)
				res.data.forEach(v => {
					v.attr_vals = v.attr_vals ? v.attr_vals.split(" ") : [],
						v.inputVisible = false
					v.inputValue = ''
				})
				// 表格中的数据赋值
				if (this.activeName === "many") {
					this.manyTableData = res.data

				} else {
					this.onlyTableData = res.data
				}
				// console.log(this.manyTableData)
				// console.log(this.onlyTableData)
				// 只能选择三级菜单
			},
			// tab页签点击事件的处理函数
			handleTabClick() {
				// console.log(this.activeName)
				this.getParamsData()
			},
			// 监听对话框的关闭事件，重置表单
			addDiolagClosed() {
				this.$refs.addFormRef.resetFields()
			},
			editiolagClosed() {
				this.$refs.editFormRef.resetFields()
			},
			// 点击确定，添加参数
			addParams() {
				// 表单预验证
				this.$refs.addFormRef.validate(async valid => {
					if (!valid) return
					const {
						data: res
					} = await this.$http.post(`categories/${this.catId}/attributes`, {
						attr_name: this.addForm.attr_name,
						attr_sel: this.activeName
					})
					if (res.meta.status !== 201) {
						return this.$message.error("参数添加失败")
					}
					this.$message.success("参数添加成功")
					this.dialogVisible = false
					this.getParamsData()
					// console.log(res)
				})

			},
			// 点击修改按钮弹出展示框
			async showDialog(attrId) {
				// 根据id查询参数
				const {
					data: res
				} = await this.$http.get(`categories/${this.catId}/attributes/${attrId}`, {
					attr_sel: this.activeName
				})
				this.editForm = res.data
				console.log(this.editForm)
				this.editDialogVisible = true
			},
			// 点击确定，修改参数
			editParams() {
				this.$refs.editFormRef.validate(async valid => {
					if (!valid) return
					const {
						data: res
					} = await this.$http.put(`categories/${this.catId}/attributes/${this.editForm.attr_id}`, {
						attr_name: this.editForm.attr_name,
						attr_sel: this.activeName
					})
					if (res.meta.status !== 200) {
						return this.$message.error("修改参数失败")
					}
					this.$message.success("修改参数成功")

					this.getParamsData()
					this.editDialogVisible = false
					console.log(res.data)
				})

			},
			// 失去焦点或者按enter键都会触发该函数
			async handleInputConfirm(row) {
				// 当文本框没有输入或者输入的全是空格时
				if(row.inputValue.trim().length === 0){
					row.inputValue = ''
					row.inputVisible = false
					return
				}
				// 如果没有return则说明文本框中有输入的内容
				row.attr_vals.push(row.inputValue.trim())
				row.inputValue = ''
				row.inputVisible = false
				// 需要发起请求,保存在这次操作
				console.log(row)
				const {data:res} = await this.$http.put(`categories/${row.cat_id}/attributes/${row.attr_id}`,{
					attr_name:row.attr_name,
					attr_sel:row.attr_sel,
					attr_vals:row.attr_vals.join(" ")
				})
				// 通过join方法将attrvals还远成字符
					// console.log(res.data)
				if(res.meta.status !== 200){
					return this.$message.error("更新失败")
				}
				this.$message.success("更新成功")
			},
			showInput(row) {
				row.inputVisible = true
				// 让文本框自动获得焦点
				// $nextTick方法的作用，就是当页面上的元素重新渲染之后，才会指定回调函数中的代码
				this.$nextTick(_ => {
					this.$refs.saveTagInput.$refs.input.focus();
				});
			},
			// 删除参数下的可选项
			async handleClose(row,index){
				row.attr_vals.splice(index,1)
				const {data:res} = await this.$http.put(`categories/${row.cat_id}/attributes/${row.attr_id}`,{
					attr_name:row.attr_name,
					attr_sel:row.attr_sel,
					attr_vals:row.attr_vals.join(' ')
				})
				
				// 通过join方法将attrvals还原成字符
					console.log(res.data)
				if(res.meta.status !== 200){
					return this.$message.error("更新失败")
				}
				this.$message.success("更新成功")
				console.log(row,index)
			}
		}
	}
</script>

<style>
	.chooseCategory {
		margin-top: 20px;
		font-size: 14px;
	}

	.el-cascader {
		margin-bottom: 20px;
	}
</style>
