<template>
	<div>
		<!-- 面包屑区域开始 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>订单管理</el-breadcrumb-item>
			<el-breadcrumb-item>订单列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!--  面包屑区域结束 -->
		<el-card>
			<!-- 搜索框区域 -->
			<el-row :gutter="20">
				<!-- 行 -->
				<el-col :span="7">
					<!-- 列 -->
					<el-input placeholder="请输入内容" clearable>
						<el-button slot="append" icon="el-icon-search"></el-button>
					</el-input>
				</el-col>
			</el-row>
			<!-- 搜索框区域结束 -->
			<!-- 表格开始 -->
			<el-table :data="orderList" style="width: 100%" border>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column prop="order_number" label="订单编号">
				</el-table-column>
				<el-table-column prop="order_price" label="订单价格" width="80">
				</el-table-column>
				<el-table-column prop="pay_status" label="是否付款" width="95">
					<template slot-scope="scope">
						<el-tag type="danger">{{scope.row.pay_status}}</el-tag>
					</template>
				</el-table-column>
				<el-table-column prop="is_send" label="是否发货" width="75"></el-table-column>
				<el-table-column prop="create_time" label="下单时间" width="180">
					<template slot-scope="scope">
						{{scope.row.create_time | dateFormat}}
					</template>
				</el-table-column>
				<el-table-column label="操作" width="150">
					<el-button type="primary" icon="el-icon-edit" size="mini" @click="showDialog"></el-button>
					<el-button type="success" icon="el-icon-location-outline" size="mini" @click="showProcessDialog"></el-button>
				</el-table-column>
			</el-table>
			<!-- 表格结束 -->
			<!-- 分页开始 -->
			<div class="block">
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum"
				 :page-sizes="[5, 7, 10,15]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper"
				 :total="total">
				</el-pagination>
			</div>
			<!-- 分页结束 -->
			<!-- 弹出修改地址对话框 -->
			<el-dialog title="修改地址" :visible.sync="dialogVisible" width="50%" @close="handleClose">
				<span slot="footer" class="dialog-footer">
					<!-- 表单 -->
					<el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
						<el-form-item label="省市区/县" prop="address1">
							<el-cascader expand-trigger="hover" v-model="addressForm.address1" :options="cityData"></el-cascader>
						</el-form-item>
						<el-form-item label="详细地址" prop="address2">
							<el-input v-model="addressForm.address2"></el-input>
						</el-form-item>
					</el-form>
					<el-button @click="dialogVisible = false">取 消</el-button>
					<el-button type="primary" @click="dialogVisible = false">确 定</el-button>
				</span>
			</el-dialog>
			<!-- 弹出物流进度对话框 -->
			<el-dialog title="物流进度" :visible.sync="process_dialogVisible" width="50%">
				<el-timeline>
				    <el-timeline-item
				      v-for="(item, index) in processInfo.data"
				      :key="index"
				      :timestamp="item.time">
				      {{item.context}}
				    </el-timeline-item>
				  </el-timeline>
				<span slot="footer" class="dialog-footer">
					<el-button @click="process_dialogVisible = false">取 消</el-button>
					<el-button type="primary" @click="process_dialogVisible = false">确 定</el-button>
				</span>
			</el-dialog>

		</el-card>
	</div>
</template>

<script>
	import cityData from "./citydata.js"
	export default {
		data() {
			return {
				queryInfo: {
					pagenum: 1,
					pagesize: 5
				},
				orderList: [],
				total: 0,
				dialogVisible: false,
				addressForm: {
					address1: [],
					address2: ''
				},
				addressFormRules: {
					address1: [{
						required: true,
						message: '请选择省市区/县',
						trigger: 'blur'
					}],
					address2: [{
						required: true,
						message: '请填写详细地址',
						trigger: 'blur'
					}],
				},
				cityData,
				process_dialogVisible: false,
				processInfo: {
					data: [{
							time: "2018-05-10 09:39:00",
							ftime: "2018-05-10 09:39:00",
							context: "已签收,感谢使用顺丰,期待再次为您服务",
							location: ""
						},
						{
							time: "2018-05-10 08:23:00",
							ftime: "2018-05-10 08:23:00",
							context: "[北京市]北京海淀育新小区营业点派件员 顺丰速运 95338正在为您派件",
							location: ""
						},
						{
							time: "2018-05-10 07:32:00",
							ftime: "2018-05-10 07:32:00",
							context: "快件到达 [北京海淀育新小区营业点]",
							location: ""
						},
						{
							time: "2018-05-10 02:03:00",
							ftime: "2018-05-10 02:03:00",
							context: "快件在[北京顺义集散中心]已装车,准备发往 [北京海淀育新小区营业点]",
							location: ""
						},
						{
							time: "2018-05-09 23:05:00",
							ftime: "2018-05-09 23:05:00",
							context: "快件到达 [北京顺义集散中心]",
							location: ""
						},
						{
							time: "2018-05-09 21:21:00",
							ftime: "2018-05-09 21:21:00",
							context: "快件在[北京宝胜营业点]已装车,准备发往 [北京顺义集散中心]",
							location: ""
						},
						{
							time: "2018-05-09 13:07:00",
							ftime: "2018-05-09 13:07:00",
							context: "顺丰速运 已收取快件",
							location: ""
						},
						{
							time: "2018-05-09 12:25:03",
							ftime: "2018-05-09 12:25:03",
							context: "卖家发货",
							location: ""
						},
						{
							time: "2018-05-09 12:22:24",
							ftime: "2018-05-09 12:22:24",
							context: "您的订单将由HLA（北京海淀区清河中街店）门店安排发货。",
							location: ""
						},
						{
							time: "2018-05-08 21:36:04",
							ftime: "2018-05-08 21:36:04",
							context: "商品已经下单",
							location: ""
						}
					]
				}
			}
		},
		created() {
			this.getOrderListInfo()
		},
		computed: {},
		methods: {
			// 获取订单列表数据
			async getOrderListInfo() {
				const {
					data: res
				} = await this.$http.get("orders", {
					params: this.queryInfo
				})
				if (res.meta.status !== 200) {
					return this.$message.error("订单列表参数获取失败")
				}
				this.orderList = res.data.goods
				this.orderList.forEach((v, i) => {
					if (v.pay_status === "0") {
						v.pay_status = "未付款"
					} else {
						v.pay_status = "已付款"
					}
				})
				this.total = res.data.total
				console.log(this.orderList)
			},
			// 每页展示多少数据
			handleSizeChange(options) {
				this.queryInfo.pagesize = options
				this.getOrderListInfo()
			},
			handleCurrentChange(options) {
				this.queryInfo.pagenum = options
				this.getOrderListInfo()
			},
			// 点击弹出修改地址框
			showDialog() {
				this.dialogVisible = true
			},
			// 关闭表单前重置表单
			handleClose() {
				this.dialogVisible = false
				this.$refs.addressFormRef.resetFields()
			},
			// 点击弹出物流进度框,请求物流信息
			async showProcessDialog() {
				this.process_dialogVisible = true
				console.log(this.processInfo)
			}

		}
	}
</script>

<style scoped>
	.el-table {
		font-size: 12px;
	}
	.el-cascader {
		width: 100%;
	}
	.el-timeline{
		font-size: 14px;
	}
</style>
