<template>
	<div>
		<!-- 面包屑区域开始 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>商品管理</el-breadcrumb-item>
			<el-breadcrumb-item>商品列表</el-breadcrumb-item>
			<el-breadcrumb-item>添加商品</el-breadcrumb-item>
		</el-breadcrumb>
		<!--  面包屑区域结束 -->
		<!-- 卡片区域 -->
		<el-card>
			<el-alert title="添加商品消息" type="info" show-icon center :closable="false"></el-alert>
			<!-- 步骤条 -->
			<el-steps :active="active - 0" align-center finish-status="success">
				<el-step title="基本信息"></el-step>
				<el-step title="商品参数"></el-step>
				<el-step title="商品属性"></el-step>
				<el-step title="商品图片"></el-step>
				<el-step title="商品内容"></el-step>
				<el-step title="完成"></el-step>
			</el-steps>
			<!-- tabs标签页 -->
			<el-tabs :tab-position="tabPosition" v-model="active" style="margin-top: 30px;font-size: 14px;" :before-leave="preventClick"
			 @tab-click="tabClicked">
				<el-tab-pane label="基本信息" name="0">
					<el-form :model="addGoodsForm" :rules="addGoodsFormRules" ref="addGoodsFormRef" label-width="100px" label-position="top">
						<el-form-item label="商品分类" prop="goods_cat">
							<div class="block">
								<el-cascader clearable v-model="addGoodsForm.goods_cat" expand-trigger="hover" :options="cateList" :props="goodsCatProps"
								 @change="handleChange"></el-cascader>
							</div>
						</el-form-item>
						<el-form-item label="商品名称" prop="goods_name">
							<el-input v-model="addGoodsForm.goods_name"></el-input>
						</el-form-item>
						<el-form-item label="商品价格" prop="goods_price">
							<el-input v-model="addGoodsForm.goods_price"></el-input>
						</el-form-item>
						<el-form-item label="商品重量" prop="goods_weight">
							<el-input v-model="addGoodsForm.goods_weight"></el-input>
						</el-form-item>
						<el-form-item label="商品数量" prop="goods_number">
							<el-input v-model="addGoodsForm.goods_number"></el-input>
						</el-form-item>
					</el-form>
				</el-tab-pane>
				<el-tab-pane label="商品参数" name="1">
					<el-form label-position="top">
						<el-form-item :label="item.attr_name" v-for="item in manyDataList" :key="item.attr_id">
							<template>
								<el-checkbox-group v-model="item.attr_vals">
									<el-checkbox :label="cb" v-for="(cb,i) in item.attr_vals" :key="i" border></el-checkbox>
								</el-checkbox-group>
							</template>
						</el-form-item>
					</el-form>
				</el-tab-pane>
				<el-tab-pane label="商品属性" name="2">
					<el-form label-position="top">
						<el-form-item :label="item.attr_name" v-for="item in onlyDataList" :key="item.attr_id">
							<el-input v-model="item.attr_vals"></el-input>
						</el-form-item>
					</el-form>
				</el-tab-pane>
				<el-tab-pane label="商品图片" name="3">
					<!-- action表示要上传到后台的api地址 -->
					<el-upload class="upload-demo" :action="uploadUrl" :on-success="handleSuccess" :on-preview="handlePreview"
					 :on-remove="handleRemove" list-type="picture" :headers="headerObj">
						<el-button size="small" type="primary">点击上传</el-button>
					</el-upload>
				</el-tab-pane>
				<el-tab-pane label="商品内容" name="4">
					<quill-editor v-model="addGoodsForm.goods_introduce"></quill-editor>
					<el-button type="primary" style="margin-top: 50px;" @click="addGoods()">添加商品</el-button>
				</el-tab-pane>
			</el-tabs>
			<!-- 弹出图片预览框 -->
			<el-dialog title="图片预览" :visible.sync="dialogVisible" width="50%">
				<img :src="imgUrl" style="width: 80%;margin:auto " />
			</el-dialog>
		</el-card>
	</div>
</template>

<script>
	import {
		quillEditor
	} from "vue-quill-editor"; //调用编辑器
	import 'quill/dist/quill.core.css';
	import 'quill/dist/quill.snow.css';
	import 'quill/dist/quill.bubble.css';
	import _ from 'lodash'
	export default {
		data() {
			return {
				dialogVisible: false,
				bl: true,
				active: 0,
				uploadUrl: 'http://127.0.0.1:8888/api/private/v1/upload',
				tabPosition: 'left',
				addGoodsForm: {
					goods_name: '',
					goods_price: 0,
					goods_weight: 0,
					goods_number: 0,
					goods_cat: [],
					pics: [],
					goods_introduce: 'ss',
					attrs: []
				},
				// 指定级联选择器的配置对象
				goodsCatProps: {
					value: 'cat_id',
					label: 'cat_name',
					children: 'children'
				},
				// 商品分类列表数据
				cateList: [],
				// 动态参数列表
				manyDataList: [],
				// 静态属性列表
				onlyDataList: [],
				// 点击预览图片的地址
				imgUrl: '',
				addGoodsFormRules: {
					goods_name: [{
						required: true,
						message: '请输入商品名称',
						trigger: 'blur'
					}],
					goods_price: [{
						required: true,
						message: '请输入商品价格',
						trigger: 'blur'
					}, ],
					goods_weight: [{
						required: true,
						message: '请输入商品重量',
						trigger: 'blur'
					}],
					goods_number: [{
						required: true,
						message: '请输入商品重量',
						trigger: 'blur'
					}],
					goods_cat: [{
						required: true,
						message: '请输入商品重量',
						trigger: 'blur'
					}],
				},
				// 商品图片上传成功但是会被拦截，因为没有token，所以通过headers加上token即可上传成功
				headerObj: {
					Authorization: window.sessionStorage.getItem('token')
				}
			}
		},
		components: {
			quillEditor
		},
		computed: {
			// 分类参数的id
			cate_id() {
				if (this.addGoodsForm.goods_cat.length === 3) {
					return this.addGoodsForm.goods_cat[2]
				}
				return null
			}
		},
		created() {
			this.getCategoryData()
		},
		methods: {
			// 文件上传成功时的钩子函数
			handleSuccess(res) {
				if (res.meta.status !== 200) {
					return this.$message.error("上传图片失败")
				}
				// 上传成功后得到一个图片的信息
				const picInfo = {
					pic: res.data.tmp_path
				}
				// 将图片信息的对象命名为pic后push到pics数组中
				this.addGoodsForm.pics.push(picInfo)
				console.log(this.addGoodsForm)
				// this.addGoodsForm.pics.push(res.data.tmp_path)
				this.$message.success("图片上传成功")
			},
			// 文件删除时的钩子函数 
			handleRemove(file) {
				// 1.获取将要删除的图片的临时路径
				const filePath = file.response.data.tmp_path
				// 2.在pics数组中找到这个图片的索引值
				const i = this.addGoodsForm.pics.findIndex(v => {
					v.pic === filePath
				})
				console.log(i)
				// 调用splice方法将图片信息从pics中移除
				this.addGoodsForm.pics.splice(i, 1)
				console.log(this.addGoodsForm)
			},
			// 点击文件列表中已上传的文件时的钩子
			handlePreview(file) {
				this.imgUrl = file.response.data.url
				this.dialogVisible = true
				console.log(file);
			},
			// 获取商品分类数据
			async getCategoryData() {
				const {
					data: res
				} = await this.$http.get("categories")
				if (res.meta.status !== 200) {
					return this.$message.error("获取商品分类数据失败")
				}
				this.cateList = res.data
				// console.log(res.data)
			},
			// 级联选择器发生改变时触发该函数
			handleChange() {
				if (this.addGoodsForm.goods_cat.length !== 3) {
					return this.addGoodsForm.goods_cat = []
				}


			},
			// 未选择分类时阻止用户点击
			// 即将离开的标签为oldActiveName，即将进入的标签页名字为activeName
			preventClick(activeName, oldActiveName) {
				if (oldActiveName === '0' && this.addGoodsForm.goods_cat.length === 0) {
					this.$message.error("请先选择商品分类")
					return false
				}
			},
			tabClicked() {
				// console.log(this.active)
				if (this.active === "1") {
					this.getManyData()
				}
				if (this.active === "2") {
					this.getOnlyData()
				}
			},
			// 请求动态的分类参数
			async getManyData() {
				var that = this
				const {
					data: res
				} = await this.$http.get(`categories/${this.cate_id}/attributes`, {
					params: {
						sel: 'many'
					}
				})
				if (res.meta.status !== 200) {
					console.log("参数列表请求失败")
				}
				res.data.forEach(v => {
					v.attr_vals = v.attr_vals.length === 0 ? [] : v.attr_vals.split(" ")
				})
				this.manyDataList = res.data
				console.log(this.manyDataList)
			},
			// 获取静态属性参数
			async getOnlyData() {
				const {
					data: res
				} = await this.$http.get(`categories/${this.cate_id}/attributes`, {
					params: {
						sel: 'only'
					}
				})
				if (res.meta.status !== 200) {
					console.log("参数列表请求失败")
				}
				this.onlyDataList = res.data
				// console.log(this.onlyDataList)
			},
			// 点击按钮实现表单预验证，成功后发起添加商品的请求
			addGoods() {
				this.$refs.addGoodsFormRef.validate(async v => {
					if (!v) {
						return this.$message.error("请填写必要的参数")
					}
					const form = _.cloneDeep(this.addGoodsForm)
					form.goods_cat = form.goods_cat.join()
					// 处理动态参数
					this.manyDataList.forEach(v => {
						const newInfo = {
							attr_id: v.attr_id,
							attr_value: ''
						}
						this.addGoodsForm.attrs.push(newInfo)
					})
					// 处理静态参数
					this.onlyDataList.forEach(v => {
						const newInfo = {
							attr_id: v.attr_id, 
							attr_value: v.attr_vals
						}
						this.addGoodsForm.attrs.push(newInfo)
					})
					form.attrs = this.addGoodsForm.attrs
					// console.log(form)
					const {data:res} = await this.$http.post("goods",form)
					console.log(res)
					if(res.meta.status !== 201){
						return this.$message.error("添加商品失败")
					}
					this.$message.success("添加商品成功")
					this.$router.push('/goods')
				})
				
				
			}
		}
	}
</script>

<style scoped>
	.el-steps {
		margin-top: 20px;
	}

	.el-checkbox {
		margin: 0 !important;
		margin-right: 15px !important;
	}

	.quill-editor {
		height: 300px;
	}
</style>
