<template>
	<div>
		<!-- 面包屑区域开始 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>商品管理</el-breadcrumb-item>
			<el-breadcrumb-item>商品列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!--  面包屑区域结束 -->
		<el-card>
			<!-- 搜索框区域 -->
			<el-row :gutter="20">
				<!-- 行 -->
				<el-col :span="7">
					<!-- 列 -->
					<el-input placeholder="请输入内容"  clearable v-model="queryInfo.query" @clear="getGoodsList">
						<el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
					</el-input>
				</el-col>
				<el-col :span="4">
					<el-button type="primary" @click="toAddGoodsPage">添加商品</el-button>
				</el-col>
			</el-row>
			<!-- 表格 -->
			<el-table :data="goodsList" border stripe>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column   prop="goods_name" label="商品名称"></el-table-column>
				<el-table-column prop="goods_price" label="商品价格(元)" width="95px"></el-table-column>
				<el-table-column prop="goods_weight" label="商品重量" width="75px"></el-table-column>
				<el-table-column prop="add_time" label="创建时间" width="140px">
					<template slot-scope="scope">
						{{scope.row.add_time | dateFormat}}
					</template>
				</el-table-column>
				<el-table-column prop="goods_state" label="操作" width="150px">
					<template slot-scope="scope">
						<el-button type="primary" icon="el-icon-edit" size="small" ></el-button>
						<el-button type="danger" icon="el-icon-delete" size="small" @click="deleteGoodsById(scope.row)" ></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!-- 表格结束 -->
			<!-- 分页功能 -->
			<div class="block">
			    <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :page-sizes="[5,7,9,12]"
			      :page-size="queryInfo.pagesize"
			      layout="total, sizes, prev, pager, next, jumper"
			      :total="total"
				  background
				  >
			    </el-pagination>
			  </div>
		</el-card>
		
	</div>
</template>

<script>
	export default{
		data(){
			return{
				queryInfo:{
					query:'',
					pagenum:1,
					pagesize:5
				},
				total:0,
				goodsList:[]
			}
		},
		created() {
			this.getGoodsList()
		},
		methods:{
			// 获取商品列表数据
			 async getGoodsList(){
				const {data:res} = await this.$http.get("goods",{params:this.queryInfo})
				if(res.meta.status !== 200){
					return this.$message.error("请求商品列表数据失败")
				}
				this.goodsList = res.data.goods
				this.total = res.data.total
				console.log(res)
			},
			// 监听每页多少条数据
			handleSizeChange(options){
				this.queryInfo.pagesize = options
				this.getGoodsList()
				// console.log(options)
			},
			// 监听当前是多少页
			handleCurrentChange(options){
				this.queryInfo.pagenum = options
				this.getGoodsList()
				console.log(options)
			},
			// 根据id删除商品
			async deleteGoodsById(options){
				const {data:res} = await this.$http.delete(`goods/${options.goods_id}`)
				if(res.meta.status !== 200){
					return this.$message.error("删除商品失败")
				}
				this.$message.success("删除商品成功")
				this.getGoodsList()
				console.log(options)
			},
			// 通过编程式导航跳转到添加商品页面
			toAddGoodsPage(){
				this.$router.push('/goods/add')
			}
		}
	}
</script> 

<style scoped>
	.el-table{
		font-size: 12px;
	}
</style>
