<template>
	<div>
		<!-- 面包屑顶部区域 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>用户管理</el-breadcrumb-item>
			<el-breadcrumb-item>用户列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!-- 卡片区域 -->
		<el-card class="box-card">
			<!-- 通过element-ui中的layout布局 -->
			<!-- 搜索框区域 -->
			<el-row :gutter="20">
				<!-- 行 -->
				<el-col :span="7">
					<!-- 列 -->
					<el-input placeholder="请输入内容" v-model="queryList.query" clearable @change="getUserList">
						<el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
					</el-input>
				</el-col>
				<el-col :span="4">
					<el-button type="primary" @click="dialogVisible = true">添加用户</el-button>
				</el-col>
			</el-row>
			<!-- 表格 -->
			<el-table :data="userList" border stripe>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column prop="username" label="姓名"></el-table-column>
				<el-table-column prop="email" label="邮箱"></el-table-column>
				<el-table-column prop="mobile" label="电话"></el-table-column>
				<el-table-column prop="role_name" label="角色"></el-table-column>
				<el-table-column prop="mg_state" label="状态">
					<template slot-scope="scope">
						<!-- 通过插槽获取scope表格中的数据 -->
						<el-switch v-model="scope.row.mg_state" @change="changeState(scope.row)">
						</el-switch>
					</template>
				</el-table-column>
				<el-table-column prop="mobile" label="操作">
					<template slot-scope="scope">
						<el-button type="primary" icon="el-icon-edit" size="small" @click="changeUserInfo(scope.row.id)"></el-button>
						<el-button type="danger" icon="el-icon-delete" size="small" @click="removeUserInfo(scope.row.id)"></el-button>
						<el-tooltip class="item" effect="dark" content="分配角色" placement="top" :enterable="false">
							<el-button type="warning" icon="el-icon-setting" size="small" @click="setRole(scope.row)"></el-button>
						</el-tooltip>
					</template>
				</el-table-column>
			</el-table>
			<!-- 分页 -->
			<div class="block">
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[1, 2, 4, 10]"
				 :page-size="queryList.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
				</el-pagination>
			</div>
		</el-card>
		<!-- 添加用户对话框 -->
		<el-dialog title="添加用户" :visible.sync="dialogVisible" width="50%" :before-close="handleClose" @close="addDialogClosed">
			<!-- 表单验证 -->
			<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
				<el-form-item label="用户名" prop="username">
					<el-input v-model="addForm.username"></el-input>
				</el-form-item>
				<el-form-item label="密码" prop="password">
					<el-input v-model="addForm.password"></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
					<el-input v-model="addForm.email"></el-input>
				</el-form-item>
				<el-form-item label="手机" prop="mobile">
					<el-input v-model="addForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取 消</el-button>
				<el-button @click="addUser" type="primary">确 定</el-button>
			</span>
		</el-dialog>
		<!-- 修改用户信息对话框 -->
		<el-dialog title="修改用户信息" :visible.sync="editDialogVisible" width="50%" :before-close="handleClose" @close="editDialogClosed">
			<el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
				<el-form-item label="用户名">
					<el-input v-model="editForm.username" disabled></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
					<el-input v-model="editForm.email"></el-input>
				</el-form-item>
				<el-form-item label="手机" prop="mobile">
					<el-input v-model="editForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="editDialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="editUserInfo">确 定</el-button>
			</span>
		</el-dialog>
		<!-- 分配角色对话框 -->
		<el-dialog title="分配角色" :visible.sync="setRoleDialogVisible" width="50%">
			<div>
				<p>当前的用户：{{userInfo.username}}</p>
				<p>当前的角色：{{userInfo.role_name}}</p>
				<p>分配新角色：<el-select v-model="selectedRoleId" placeholder="请选择">
						<el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id">
						</el-option> 
					</el-select>
				</p>
			</div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="setRoleDialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="putSetRole">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			//验证邮箱的规则
			var checkEmail = (rule, value, cb) => {
				//验证邮箱的正则表达式
				const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
				if (regEmail.test(value)) {
					//合法的邮箱
					return cb()
				}
				cb(new Error('请输入合法的邮箱'))
			};
			//验证手机号的规则
			var checkMobile = (rule, value, cb) => {
				//验证手机号的正则表达式
				const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
				if (regMobile.test(value)) {
					//合法的手机号
					return cb()
				}
				cb(new Error('请输入合法的手机号'))
			};
			return {

				value: true,
				queryList: {
					query: '',
					pagenum: 1,
					pagesize: 4,
				},
				dialogVisible: false,
				editDialogVisible: false,
				setRoleDialogVisible: false,
				userList: [],
				//数据显示的总条数
				total: 0,
				//修改用户信息
				addForm: {
					username: '',
					password: '',
					email: '',
					mobile: '',
				},
				editForm: {},
				editFormRules: {
					email: [{
							required: true,
							message: '请输入邮箱',
							trigger: 'blur'
						},
						{
							validator: checkEmail,
							trigger: 'blur'
						}
					],
					mobile: [{
							required: true,
							message: '请输入手机号',
							trigger: 'blur'
						},
						{
							validator: checkMobile,
							trigger: 'blur'
						}
					],
				},
				addFormRules: {
					username: [{
							required: true,
							message: '请输入用户名',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: '长度在 3 到 10 个字符',
							trigger: 'blur'
						}
					],
					password: [{
							required: true,
							message: '请输入密码',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: '长度在 3 到 10 个字符',
							trigger: 'blur'
						}
					],
					email: [{
							required: true,
							message: '请输入邮箱',
							trigger: 'blur'
						},
						{
							validator: checkEmail,
							trigger: 'blur'
						}
					],
					mobile: [{
							required: true,
							message: '请输入手机号',
							trigger: 'blur'
						},
						{
							validator: checkMobile,
							trigger: 'blur'
						}
					],
				},
				// 需要被分配角色的用户信息
				userInfo: {},
				// 所有角色数据列表
				rolesList:[],
				selectedRoleId:''
			}
		},
		created() {
			this.getUserList()
		},
		methods: {
			//添加用户界面开关
			handleClose() {
				this.dialogVisible = false
				this.editDialogVisible = false
			},
			//监听每页多少条数据
			handleSizeChange(val) {
				// console.log(`每页 ${val} 条`);
				this.queryList.pagesize = val
				this.getUserList()
			},
			//监听当前的页数
			handleCurrentChange(val) {
				// console.log(`当前页: ${val}`);
				this.queryList.pagenum = val
				this.getUserList()
			},
			//监听switch开关变化，并将状态传入后台数据库
			async changeState(rowinfo) {
				// console.log(rowinfo)
				const {
					data: res
				} = await this.$http.put(`users/${rowinfo.id}/state/${rowinfo.mg_state}`)
				// console.log(res)
				if (res.meta.status !== 200) {
					//更新失败的话返回之前的状态
					rowinfo.mg_state = !rowinfo.mg_state
					return this.$message.error("更新用户状态失败")
				}
				this.$message.success("更新用户状态成功")
			},
			async getUserList() {
				const {
					data: res
				} = await this.$http.get('users', {
					params: this.queryList
				})
				// console.log(res)
				if (res.meta.status != 200) return this.$message.error('获取列表数据失败')
				this.userList = res.data.users
				// console.log(this.userList)
				this.total = res.data.total
				// console.log(this.userList)
			},
			//关闭表单时重置表单
			addDialogClosed() {
				this.$refs.addFormRef.resetFields()
			},
			//关闭表单时重置修改表单
			editDialogClosed() {
				this.$refs.editFormRef.resetFields()
			},
			//点击确定按钮，添加新用户npm 
			addUser() {
				this.$refs.addFormRef.validate(async v => {
					//如果表单预验证不正确就直接返回
					if (!v) return
					//验证正确发起添加用户请求
					const {
						data: res
					} = await this.$http.post("users", this.addForm)
					// console.log(res)
					//请求不成功，提示用户添加失败
					if (res.meta.status !== 201) {
						this.$message.error("添加用户失败")
					}
					this.$message.success("添加用户成功")
					this.getUserList()
					this.dialogVisible = false
				})
			},
			//点击确定按钮修改用户信息，预验证表单
			editUserInfo() {
				const tse = {
					email: this.editForm.email,
					mobile: this.editForm.mobile
				}
				this.$refs.editFormRef.validate(async v => {
					//如果表单预验证错误就直接返回
					if (!v) return
					const {
						data: res
					} = await this.$http.put(`users/${this.editForm.id}`, tse)
					//另一种写法
					// this.$http.put('users' + this.editForm.id,{email:this.editForm.email,
					// mobile:this.editForm.mobile
					// })
					if (res.meta.status !== 200) {
						return this.$message.error("修改用户信息失败")
					}
					this.$message.success("修改用户信息成功")
					console.log(res)
				})
				this.editDialogVisible = false
				this.getUserList()
			},
			//修改用户信息
			async changeUserInfo(id) {
				// console.log(userinfo)
				//通过表格中的scope获取到了对应用户的id,根据id请求到对应用户的信息
				const {
					data: res
				} = await this.$http.get(`users/${id}`)
				console.log(res)
				if (res.meta.status !== 200) {
					return this.$message.error("请求用户信息失败")
				}
				this.editForm = res.data
				this.editDialogVisible = true
			},
			//根据id删除对应的用户信息 
			removeUserInfo(id) {
				//删除前弹框询问用户是否删除数据
				this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(async () => {
					const {
						data: res
					} = await this.$http.delete(`users/${id}`)
					this.getUserList()
					console.log(res)
					this.$message({
						type: 'success',
						message: '删除成功!'
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			},
			// 分配角色
			async setRole(userInfo) {
				this.userInfo = userInfo
				// 展示对话框前获取所有的列表信息
				const {
					data: res
				} = await this.$http.get("roles")
				if (res.meta.status !== 200) {
					return this.$message.error("请求角色列表数据失败")
				}
				this.rolesList = res.data
				// console.log(this.rolesList)
				this.setRoleDialogVisible = true
				// console.log(userInfo)
			},
			// 点击确定后提交分配角色请求
			async putSetRole(){
				if(!this.selectedRoleId){
					return this.$message.error("清闲选择要分配的角色")
				}
				const {data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{rid:this.selectedRoleId})
				if(res.meta.status !== 200){
					return this.$message.error("请求角色分配失败")
				}
				this.$message.success("角色分配成功")
				this.getUserList()
				this.setRoleDialogVisible = false
				// console.log(res)
				// console.log(this.selectedRoleId)
			}
				
		},

	}
</script>

<style>
	.el-card {
		margin-top: 20px;
	}

	.el-table {
		margin-top: 15px;
	}

	.block {
		margin-top: 15px;
	}
	p{
		font-size: 12px;
		margin: 10px;
	}
</style>
