<template>
	<div class="container">
		<div class="login-moudle">
			<div class="login-img">
				<img src="../assets/logo.png" />
			</div>
			<div class="demo-input-suffix">
				<el-form :rules="rules" ref="ruleForm" :model="ruleForm">
					<el-form-item prop="username">
						<el-input placeholder="请输入用户名" prefix-icon="el-icon-user" class="input1" v-model="ruleForm.username">
						</el-input>
					</el-form-item>
					<el-form-item prop="password">
						<el-input placeholder="请输入密码" prefix-icon="el-icon-lock" v-model="ruleForm.password" type="password">
						</el-input>
					</el-form-item>
				</el-form>
			</div>
			<el-row class="btn">
					<el-button type="primary" @click="login">登录</el-button>
					<el-button type="success" @click="resetForm">重置</el-button>
			</el-row>
		</div>
	</div>

</template>

<script>
	export default {
		data() {
			return {
				ruleForm: {
					username: 'admin',
					password: '123456',
				},
				rules: {
					username: [{
							required: true,
							message: '用户名至少六个字符',
							trigger: 'blur'
						},
						{ 
							min: 4,
							max: 10,
							message: '用户名长度在 6 到 10 个字符',
							trigger: 'blur'
						}
					],
					password: [{
							required: true,
							message: '密码至少六个字符',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: '密码至少6个字符',
							trigger: 'blur'
						}
					],
				}
			}
		},
		methods: {
			//重置表单
			resetForm(){
				// console.log(this)
				this.$refs.ruleForm.resetFields()
			},
			//表单预验证
			login(){
				this.$refs.ruleForm.validate(async (vaild)=>{
					if(!vaild) return 
					// console.log(vaild)
					//通过axios发起pst登录请求
					//不使用 async await会返回一个promise对象
					//将data解构赋值同时重命名为res
					const {data:res} = await this.$http.post("login",this.ruleForm)
					// console.log(res)
					//也可以直接写为
					// const res= await this.$http.post("login",this.ruleForm)
					// console.log(res.data)
					if(res.meta.status!=200) return this.$message.error("登陆失败")
					this.$message.success("登陆成功")
					/*
						1.将登陆成功后的token 保存到客户端的sessionStorage中
							1.1项目中除了登陆以外的其他api接口，必须在登录后才可以访问
							1.2 token只应在当前网站打开期间生效，所以将token保存在sessionStorage中
						2.通过编程式导航跳转到后台主页，路由地址是/index
					*/
				   window.sessionStorage.setItem("token",res.data.token)
				   this.$router.push("/index")
				})
			}
		}
	} 
</script>

<style scoped>
	.el-button--primary {
		margin-right: 10px;
	}

	a {
		text-decoration: none;
	}

	.container {
		width: 100%;
		height: 100%;
		background-color: #0085A1;
	}

	.login-moudle {
		width: 500px;
		height: 350px;
		background-color: white;
		position: absolute;
		display: flex;
		flex-direction: column;
		align-items: center;
		top: 50%;
		left: 50%;
		transform: translate(-250px, -175px);
	}
	.login-img {
		width: 122px;
		height: 122px;
		border: 2px solid #ffffff;
		border-radius: 50%;
		position: absolute;
		top: -61px;
		left: 173px;
		z-index: 99;
		box-shadow: 0 0 8px;
	}
	.login-moudle img {
		width: 120px;
		height: 120px;
		border: 1px solid #ffffff;
		background-color: #e5e5e5;
		border-radius: 50%;
	}

	.demo-input-suffix {
		margin-top: 100px;
		width: 70%;
	}

	.input1 {
		margin-bottom: 30px;
	}
	.btn {
		margin-top: 40px;
		margin-left: 200px;
	}
</style>
