<template>
	<div v-if="this.cateList.length !== 0">
		<!-- 面包屑区域开始 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>商品管理</el-breadcrumb-item>
			<el-breadcrumb-item>商品分类</el-breadcrumb-item>
		</el-breadcrumb>
		<!--  面包屑区域结束 -->
		<!-- 卡片区域开始 -->
		<el-card class="box-card">
			<el-row>
				<el-col>
					<el-button type="primary" @click="showAddDialogVisible">添加分类</el-button>
				</el-col>
			</el-row>
			<!-- 表格   vue-table-with-tree-->
			<tree-table class="tree-table" :data="cateList" :columns="columns" :selection-type="false" show-index index-text="序号"
			 :expand-type="false" border>
				<!-- 是否有效 -->
				<template slot="isok" slot-scope="scope">
					<i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: #69c83c;"></i>
					<i class="el-icon-error" v-else style="color: red;"></i>
				</template>
				<!-- 排序 -->
				<template slot="order" slot-scope="scope">
					<el-tag size="mini" v-if="scope.row.cat_level === 0">一级</el-tag>
					<el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1">二级</el-tag>
					<el-tag type="warning" size="mini" v-else>三级</el-tag>
				</template>
				<!-- 操作 -->
				<template slot="options" slot-scope="scope">
					<el-button type="primary" icon="el-icon-edit" size="mini">编辑</el-button>
					<el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
				</template>
			</tree-table>
			<!-- 分页 -->
			<div class="block">
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum"
				 :page-sizes="[3,5,7,10]" :page-size="queryInfo.pagesize" layout="total, sizes,prev, pager, next, jumper" :total="total">
				</el-pagination>
			</div>
		</el-card>
		<!-- 卡片区域结束 -->

		<!-- 点击添加分类按钮弹出展示框 -->
		<el-dialog title="添加分类" :visible.sync="dialogVisible" width="50%" @close="resetDialog">
			<!-- 表单区域 -->
			<el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
				<el-form-item label="分类名称" prop="cat_name">
					<el-input v-model="addCateForm.cat_name"></el-input>
				</el-form-item>
				<el-form-item label="父级分类">
					<!-- 级联选择器 -->
					<!-- options 指定数据源 -->
					<!-- props 指定配置对象 -->
					<el-cascader expand-trigger="hover" :options="parentCategories" :props="cascaderProps" v-model="selectedKeys"
					 @change="parentCateChanged" clearable change-on-select="true"></el-cascader>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="addCategories">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				// 商品分类的数据列表，默认为空
				cateList: [],
				// 查询条件
				queryInfo: {
					type: 3,
					pagenum: 1,
					pagesize: 5
				},
				total: 0,
				columns: [{
						label: '分类名称',
						prop: 'cat_name'
					},
					{
						label: '是否有效',
						// 表示，将当前列定义为模板列
						type: 'template',
						// 表示当前这一列使用的模板名称
						template: 'isok'
					},
					{
						label: '排序',
						// 表示，将当前列定义为模板列
						type: 'template',
						// 表示当前这一列使用的模板名称
						template: 'order'
					},
					{
						label: '操作',
						// 表示，将当前列定义为模板列
						type: 'template',
						// 表示当前这一列使用的模板名称
						template: 'options'
					},
				],
				dialogVisible: false,
				addCateForm: {
					// 将要添加的分类的名称
					cat_name: '',
					cat_pid: 0,
					cat_level: 0
				},
				// 添加分类表单的验证规则
				addCateFormRules: {
					cat_name: [{
							required: true,
							message: '请输入分类名称',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: '长度在 3 到 10 个字符',
							trigger: 'blur'
						}
					],
				},
				// 父级分类数据列表
				parentCategories: [],
				// 指定级联选择器的配置对象
				cascaderProps: {
					value: 'cat_id',
					label: 'cat_name',
					children: 'children'
				},
				// 选中父级分类的id分组
				selectedKeys: []
			}
		},
		created() {
			this.getCateList()
		},
		methods: {
			// 获取商品分类数据
			async getCateList() {
				const {
					data: res
				} = await this.$http.get("categories", {
					params: this.queryInfo
				})
				if (res.meta.status !== 200) {
					return this.$message.error("获取商品分类数据失败")
				}
				// 将请求到的商品数据赋值给cateList
				this.cateList = res.data.result
				this.total = res.data.total
				// console.log(this.cateList)
			},
			handleSizeChange(val) {
				// console.log(val)
				this.queryInfo.pagesize = val
				this.getCateList()
			},
			handleCurrentChange(val) {
				this.queryInfo.pagenum = val
				this.getCateList()
				// console.log(val)
			},
			// 展示添加分类区域
			showAddDialogVisible() {
				this.getParentCategories()
				this.dialogVisible = true
			},
			// 获取父级分类数据
			async getParentCategories() {
				const {
					data: res
				} = await this.$http.get("categories", {
					params: {
						type: 2
					}
				})
				if (res.meta.status !== 200) {
					return this.$message.error("获取父级分类数据失败")
				}
				this.parentCategories = res.data
				// console.log(res.data)
			},
			// 级联选择器选项发生变化时触发这个函数
			parentCateChanged() {
				// console.log(this.selectedKeys)
				// 如果selectedKeys数组中的 length 大于 0 ，证明选中的父级分类
				// 反之，没有选中任何父级分类
				if (this.selectedKeys.length > 0) {
					// 父级分类的id
					this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
					// 为当前分类的等级赋值
					this.addCateForm.cat_level = this.selectedKeys.length
				} else {
					this.addCateForm.cat_pid = 0
					this.addCateForm.cat_level = 0
				}


			},
			// 关闭展示框时重置表单
			resetDialog() {
				this.$refs.addCateFormRef.resetFields()
				this.selectedKeys = []
				this.addCateForm.cat_pid = 0
				this.addCateForm.cat_level = 0
			},
			// 点击确定请求数据，添加商品分类
			addCategories() {
				this.$refs.addCateFormRef.validate(async v => {
					if (!v) return
					const {
						data: res
					} = await this.$http.post("categories", this.addCateForm)
					if (res.meta.status !== 201) {
						return this.$message.error("添加商品分类失败")
					}
					this.$message.success("商品分类添加成功")
				})
				this.getCateList()
				// console.log(res)
				this.dialogVisible = false
			}
		}
	}
</script>

<style scoped>
	.tree-table {
		margin-top: 10px;
	}

	.el-cascader {
		width: 100%;
	}
</style>
