<template>
	<div>
		<!-- 面包屑区域开始 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>权限管理</el-breadcrumb-item>
			<el-breadcrumb-item>权限列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!--  面包屑区域结束 -->
		<!-- 卡片区域开始 -->
		<el-card class="box-card">
			<!-- 表格 -->
			<el-table :data="limitsInfo" border stripe>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column prop="authName" label="权限名称"></el-table-column>
				<el-table-column prop="path" label="路径"></el-table-column>
				<el-table-column prop="level" label="权限等级">
					<template slot-scope="scope">
						<el-tag :type="scope.row.color">{{scope.row.levelText}}</el-tag>
					</template>
				</el-table-column>
			</el-table>
			<!-- <el-table-column prop="authName" label="权限名称"></el-table-column> --> 
		</el-card>
		<!-- 卡片区域结束 --> 
	</div>
</template>
<script>
	export default{
		data(){
			return{
				limitsInfo:[],
				type:'list',
				color:'',
				levelText:''
			}
		},
		created() {
			this.getLimitsInfo()
		},
		methods:{
			// 根据标签级别显示不同颜色和等级
			levelTag(){
				this.limitsInfo.forEach(v=>{
					if(v.level == 0){
						this.color = 'primary'
						this.levelText = '一级'
					}else if(v.level == 1){
						this.color = 'success'
						this.levelText = '二级'
					}else{
						this.color = 'warning'
						this.levelText = '三级'
					}
					v.color = this.color
					v.levelText = this.levelText
				})
				// console.log(this.color)
			},
			async getLimitsInfo(){
				const {data:res} = await this.$http.get(`rights/${this.type}`)
				if(res.meta.status !==200){
					return this.$message.error("获取权限列表失败")
				}
				this.limitsInfo = res.data
				// console.log(this.limitsInfo)
				this.levelTag()
			}
		}
	}
</script>
