<template>
	<div>
		<!-- 面包屑区域开始 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>权限管理</el-breadcrumb-item>
			<el-breadcrumb-item>角色列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!--  面包屑区域结束 -->
		<!-- 卡片区域开始 -->
		<el-card class="box-card">
			<el-button type="primary">添加角色</el-button>
			<!-- 表格 -->
			<el-table :data="roleInfoList" border stripe>
				<el-table-column type="expand">
					<template slot-scope="scope">
						<!-- 动态添加两种样式，1.给每个一级菜单加底部边框 2.给索引值为1的菜单加顶部边框 -->
						<el-row :class="['bdbottom', index1 === 0?'bdtop':'']" v-for="(item1,index1) in scope.row.children" :key="item1.id">
							<!-- 渲染一级权限 -->
							<el-col :span="5">
								<el-tag closable @close="removeRoleListById(scope.row.id,item1.id)"> {{item1.authName}}</el-tag>
								<i class="el-icon-caret-right"></i>
							</el-col>
							<!-- 渲染二级和三级菜单 -->
							<el-col :span="19">
								<!-- 嵌套渲染二级权限 -->
								<!-- 当二级菜单索引值为0是，不加顶部边框因为会跟一级菜单重复，当索引值不是0时加顶部边框 -->
								<el-row :class="index2 === 0?'':'bdtop'" v-for="(item2,index2) in item1.children" :key="item2.id">
									<el-col :span="6">
										<el-tag @close="removeRoleListById(scope.row.id,item2.id)" closable type="success"> {{item2.authName}}</el-tag>
										<i class="el-icon-caret-right"></i>
									</el-col>
									<el-col :span="18">
										<el-tag @close="removeRoleListById(scope.row.id,item3.id)" type="warning" v-for="(item3) in item2.children"
										 :key="item3.id" closable> {{item3.authName}}</el-tag>
									</el-col>
								</el-row>
							</el-col>
						</el-row>
					</template>
				</el-table-column>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column prop="roleName" label="角色名称"></el-table-column>
				<el-table-column prop="roleDesc" label="角色描述"></el-table-column>
				<el-table-column label="操作">
					<template slot-scope="scope">
						<el-button type="primary" icon="el-icon-edit" size="small">编辑</el-button>
						<el-button type="danger" icon="el-icon-delete" size="small">删除</el-button>
						<el-tooltip class="item" effect="dark" content="分配权限" placement="top" :enterable="false">
							<el-button type="warning" icon="el-icon-setting" size="small" @click="setRightDialogVisible(scope.row)">分配权限</el-button>
						</el-tooltip>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
		<!-- 卡片区域结束 -->
		<!-- 弹出分配权限区域 -->
		<el-dialog title="分配权限" :visible.sync="dialogVisible" width="50%" @close="setRightDialogClosed">
			<!-- 通过element-ui 中的Tree 树形控件来展示权限分配 -->
			<el-tree :data="limitsInfo" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef">
			</el-tree>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="allotRights">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				roleInfoList: [],
				dialogVisible: false,
				type: 'tree',
				limitsInfo: [], 
				// 树形控件绑定数据
				treeProps: {
					children: 'children', 
					label: 'authName'
				},
				// 树形组件默认选中的节点，id值数组
				defKeys:[],
				roleId:''
			}
		},
		created() {
			this.getroleInfoList()
		},
		methods: {
			// 获取角色列表数据
			async getroleInfoList() {
				const {
					data: res
				} = await this.$http.get("roles")
				if (res.meta.status !== 200) {
					return this.$message.error("请求角色列表数据失败")
				}
				this.roleInfoList = res.data
				// console.log(this.roleInfoList)
			},
			//点击删除权限按钮
			removeRoleListById(roleId, rightId) {
				//删除前弹框询问用户是否删除数据
				this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(async () => {
					const {
						data: res
					} = await this.$http.delete(`roles/${roleId}/rights/${rightId}`)
					this.getroleInfoList()
					this.$message({
						type: 'success',
						message: '删除成功!'
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			},
			// 点击弹出分配权限窗口,请求所有权限列表数据
			async setRightDialogVisible(role) {
				const {
					data: res
				} = await this.$http.get(`rights/${this.type}`)
				if (res.meta.status !== 200) {
					return this.$message.error("获取权限列表失败")
				} 
				this.limitsInfo = res.data
				this.roleId = role.id
				// console.log(this.limitsInfo)
				this.getLeafKeys(role,this.defKeys)
				this.dialogVisible = true
				console.log(role)
			},
			// 加载当前角色已有权限,使用递归函数
			getLeafKeys(node,arr){
				// 如果当前node节点不包含children属性，则是三级节点
				if(!node.children){
					// 将三级节点id添加到defKeys数组中
					return arr.push(node.id)
				}
				node.children.forEach((item)=>{
					this.getLeafKeys(item,arr)
				})
			},
			// 监听分配权限对话框的关闭事件
			setRightDialogClosed(){
				this.defKeys = []
			},
			// 调用api完成分配权限功能
			async allotRights(){
				// 不加...keys数组为[[150],[101,104]],加...keys数组展开为[150,101,104]
				const keys = [
					...this.$refs.treeRef.getCheckedKeys(),
					...this.$refs.treeRef.getHalfCheckedKeys(),
					
				]
				// join() 方法用于把数组中的所有元素放入一个字符串。不加,idStr为101102  加,idStr为101,102
				const idStr = keys.join(",")
				// 调用api进行权限分配
				const {data:res} = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
				if(res.meta.status !==200){
					return this.$message.error("分配权限失败")
				}
				this.$message.success("分配权限成功")
				// 重新 获取角色列表数据
				this.getroleInfoList()
				// console.log(res)
				this.dialogVisible = false
				// console.log(idStr) 
			}
		}
	}
</script>

<style>
	.el-tag {
		margin: 10px;
	}

	.bdtop {
		border-top: 1px solid #eee;
	}

	.bdbottom {
		border-bottom: 1px solid #eee;
	}
</style>
